// ----- Palette Functions ----- //
/**
 * Picks a lightness of colour for a palette corresponding to the given pillar
 * N.b. it does not handle non-pillar themes
 * @param pillar
 * @param lightness
 */
const pillarPalette = (pillar, lightness) => {
    switch (pillar) {
        case Pillar.News:
            return sourcePalette.news[lightness];
        case Pillar.Lifestyle:
            return sourcePalette.lifestyle[lightness];
        case Pillar.Sport:
            return sourcePalette.sport[lightness];
        case Pillar.Culture:
            return sourcePalette.culture[lightness];
        case Pillar.Opinion:
            return sourcePalette.opinion[lightness];
    }
};
const textblockBulletLight = ({ theme, design }) => {
    switch (theme) {
        case Pillar.News: {
            return design === ArticleDesign.Analysis
                ? sourcePalette.news[300]
                : sourcePalette.news[400];
        }
        case Pillar.Opinion:
        case Pillar.Sport:
        case Pillar.Culture:
        case Pillar.Lifestyle: {
            return pillarPalette(theme, 400);
        }
        case ArticleSpecial.Labs: {
            return sourcePalette.neutral[7];
        }
        case ArticleSpecial.SpecialReport: {
            return sourcePalette.specialReport[300];
        }
        case ArticleSpecial.SpecialReportAlt: {
            return sourcePalette.specialReportAlt[200];
        }
    }
};
const textblockTextLight = ({ design }) => {
    switch (design) {
        case ArticleDesign.Audio:
            return sourcePalette.neutral[97];
        default:
            return 'inherit';
    }
};
const textblockTextDark = () => 'inherit';
const headlineTextLight = ({ design, display, theme }) => {
    switch (display) {
        case ArticleDisplay.Immersive:
            return sourcePalette.neutral[97];
        default: {
            switch (design) {
                case ArticleDesign.Editorial:
                case ArticleDesign.Feature:
                case ArticleDesign.Recipe:
                case ArticleDesign.Review: {
                    switch (theme) {
                        case ArticleSpecial.SpecialReportAlt:
                            return sourcePalette.specialReportAlt[200];
                        case ArticleSpecial.SpecialReport:
                            return sourcePalette.specialReport[200];
                        case ArticleSpecial.Labs:
                            return sourcePalette.labs[200];
                        default:
                            return pillarPalette(theme, 200);
                    }
                }
                case ArticleDesign.LiveBlog: {
                    switch (theme) {
                        case ArticleSpecial.SpecialReport:
                            return sourcePalette.specialReport[200];
                        default:
                            return sourcePalette.neutral[100];
                    }
                }
                case ArticleDesign.DeadBlog: {
                    switch (theme) {
                        case ArticleSpecial.SpecialReport:
                            return sourcePalette.specialReport[200];
                        default:
                            return sourcePalette.neutral[7];
                    }
                }
                case ArticleDesign.Interview:
                case ArticleDesign.Picture:
                case ArticleDesign.Audio:
                case ArticleDesign.Video:
                    switch (theme) {
                        case ArticleSpecial.Labs:
                            return sourcePalette.neutral[7];
                        default:
                            return sourcePalette.neutral[97];
                    }
                default:
                    return sourcePalette.neutral[7];
            }
        }
    }
};
